import React from 'react';

class Header extends React.Component {
  render() {
    return (
      <header>
        <div id="back-to-home">
          <a href="https://esxbot.github.io"><img src="https://esxbot.github.io/assets/image/arrow_left.png" alt="" /></a>
        </div>
        <div title="Crie graficamente JSON para as publicações de mensagens Discord e use-as com vários bots / aplicativos baseados na Discord API.">Incorporar Construtor</div>
      </header>
    );
  }
}

export default Header;



